<template lang="html">
  <div class="__cm0container" @click="containerClick($event)">
    <div class="__cm1header">
      <!-- this is header -->
    </div>
    <div class="__cm2board-header">
      <span class="__cm4title __cm3left">
        <!-- Vue Plan -->
      </span>
      <span class="__cm3left">
        <!-- 😂 -->
      </span>
      <span>
        <!-- 😱 Private -->
      </span>
      <a class="__cm5menu">
        Show Menu
      </a>
    </div>
    <div class="__cm6panel">
      <board-panel></board-panel>
    </div>
  </div>
</template>

<script>
var cssModules = {"container":"__cm0container","header":"__cm1header","board-header":"__cm2board-header","left":"__cm3left","title":"__cm4title","menu":"__cm5menu","panel":"__cm6panel"}
import boardPanel from './boardPanel'
import eventHub, { CLOSE_HEADER_OPTION } from './eventHub'

export default {
  data () {
    return {}
  },
  computed: {},
  mounted () {},
  methods: {
    containerClick (e) {
      if (e.target.className.indexOf('header-option') === -1) {
        eventHub.$emit(CLOSE_HEADER_OPTION)
      }
    }
  },
  components: {
    boardPanel
  }
}
</script>

<style lang="scss" css-modules>
.__cm0container {
  height: 100%;
  font-size: 14px;
}
.__cm1header {
  height: 40px;
  line-height: 40px;
  color: #fff;
  background-color: rgba(0, 0, 0, .3);
}
.__cm2board-header {
  height: 46px;
  padding: 8px 4px 8px 18px;
  line-height: 30px;
  color: #fff;
  .__cm3left {
    margin-right: 6px;
  }
  .__cm4title {
    font-size: 18px;
  }
  .__cm5menu {
    height: 30px;
    line-height: 30px;
    text-decoration: underline;
    font-size: 9pt;
    float: right;
    padding-right: 22px;
    position: relative;
    top:50%;
    transform: translateY(-50%);
    cursor: pointer;
  }
}
.__cm6panel {
  position:absolute;
  top:0;
  padding-top:86px;
  height:100%;
  width:100%;
}
</style>



