<template lang="html">
  <div class="__cm7container" ref="container">
    <div class="__cm8list" drag v-for="list in boardData.list">
      <boarder-header :list-id="list.list_id" :name="list.list_name"></boarder-header>
      <div class="__cm9list-card listcard" ref="listcard">
        <Card v-for="card in list.cards">
          {{card.text}}
        </Card>
      </div>
      <add-card :list-id="list.list_id"></add-card>
    </div>
    <add-list></add-list>
    <list-option></list-option>
  </div>
</template>

<script>
var cssModules = {"container":"__cm7container","list":"__cm8list","list-card":"__cm9list-card"}
import dragable from './drag'
import boarderHeader from './boardheader'
import Card from './boardCard'
import addCard from './addcard'
import addList from './addlist'
import listOption from './listOption'
var listcard = null
var container = null

export default {
  data () {
    return {}
  },
  computed: {
    boardData () {
      return this.$store.state.current_board
    }
  },
  mounted () {
    this.getBoardData(this.$route.params.board_id)
  },
  updated () {
    if (this.$store.state.current_board.list.length > 0) {
      this.$nextTick(() => {
        if (listcard && container) {
          this.$nextTick(() => {
            listcard.update()
            container.update()
          })
        } else {
          listcard = dragable(this.$refs.listcard)
          container = dragable(this.$refs.container)
        }
      })
    }
  },
  methods: {
    getBoardData: function (id) {
      this.$store.dispatch('FETCH_BOARD_DATA', { id })
    }
  },
  components: {
    boarderHeader,
    Card,
    addCard,
    addList,
    listOption
  }
}
</script>

<style lang="scss" css-modules>
.__cm7container {
  height: 100%;
  padding: 8px;
  white-space:nowrap;
  user-select: none;
  -webkit-user-select:none;
  -moz-user-select: none;
  * {
     user-select: none;
     -webkit-user-select:none;
     -moz-user-select: none;
  }
}
.__cm8list {
  width: 270px;
  border-radius: 4px;
  background-color: #e2e4e6;
  cursor: pointer;
  display: inline-block;
  vertical-align: top;
  margin-left: 10px;
  user-select: none;
  -webkit-user-select:none;
  -moz-user-select: none;
  font-size: 14px;
  .__cm9list-card {
    padding: 10px;
  }
}
</style>
<style lang="scss">
.card {
  margin: 10px 0;
  border-bottom: 2px solid #D6DADC;
  &:hover {
    background-color: #EDEFF0;
  }
  &:first-child {
    margin-top: 0px;
  }
  &:last-child {
    margin-bottom: 0px;
  }
}
</style>



