<template lang="html">
  <div class="__cm31options" :style="{ left: left + 'px', top: top + 'px' }" v-show="showOption">
    <ul class="__cm32option-ul">
      <li class="__cm33item" @click="optionClick('remove list')">
        <span>REMOVE LIST</span>
      </li>
      <!-- <li class="__cm33item" @click="optionClick('add list')">
        <span>ADD LIST</span>
        </li>
      <li class="__cm33item" @click="optionClick('add card')">
        <span>ADD CARD</span>
      </li> -->
    </ul>
  </div>
</template>

<script>
var cssModules = {"options":"__cm31options","option-ul":"__cm32option-ul","item":"__cm33item"}
import eventHub, { OPEN_HEADER_OPTION, CLOSE_HEADER_OPTION } from './eventHub'

export default {
  data () {
    return {
      left: 0,
      top: 0,
      listId: '',
      showOption: false
    }
  },
  mounted () {
    eventHub.$on(OPEN_HEADER_OPTION, ({ left, bottom, list_id }) => {
      this.left = left
      this.top = bottom
      this.listId = list_id
      this.showOption = true
    })
    eventHub.$on(CLOSE_HEADER_OPTION, () => {
      this.showOption = false
    })
  },
  methods: {
    optionClick (type) {
      switch (type) {
        case 'remove list':
          return this.$store.dispatch('REMOVE_LIST', { board_id: this.$route.params.board_id, list_id: this.listId })
        case 'add list':
          return
        case 'add card':
          return
      }
    }
  },
  destroyed () {
    eventHub.$off(OPEN_HEADER_OPTION)
    eventHub.$off(CLOSE_HEADER_OPTION)
  }
}
</script>

<style lang="scss" css-modules>
.__cm31options {
  position: fixed;
  // height: 120px;
  width: 200px;
  background-color: #fff;
}
.__cm32option-ul {
  margin: 0;
  padding: 0;
  list-style: none;
  height: 100%;
  .__cm33item {
    height: 40px;
    text-align: center;
    font-size: 16px;
    font-weight: bold;
    position: relative;
    cursor: pointer;
    &:hover {
      color: lighten(#000, 10%);
      background-color: darken(#fff, 10%);
    }
    &:active {
      color: lighten(#000, 20%);
      background-color: darken(#fff, 15%);
    }
    span {
      display: block;
      position: relative;
      top: 50%;
      transform: translateY(-50%);
    }
  }
}
</style>



